.delete-button {
  border:none;
  border-radius: 4px;
  padding: 5px 10px;
  margin-top:15px;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  color: white;
  background-color: rgb(216, 27, 27);
  text-align: center;
  transition: all 0.2s;
    
  }

  .delete-button:hover{
    background-color: brown;
    cursor: pointer;
  }